/* Styling for the orange shape on the left */
.orange-shape {
    position: absolute;
    left: -50px;
    top: 50px;
    width: 300px;
    height: 300px;
    background: orange;
    clip-path: polygon(0 0, 100% 0, 85% 100%, 15% 100%);
  }
  
  /* Benefits Section Styling */
  /* styles.css or Benefits.css */
#benefits {
  padding-top: calc(64px + 1rem); /* 64px navbar height + 1rem extra spacing */
}

  
  /* Benefit item container styling */
  .benefit-item {
    transition: transform 0.3s ease;
  }
  
  .benefit-item:hover {
    transform: translateY(-5px);
  }
  
  /* Icon styling */
  .icon img {
    max-width: 100%;
    height: auto;
  }